import React from "react";

function List(props) {
  const list = props.data.map(item => (
    <li key={item} className="row ml-0 mt-4 border-bottom font-small">
      <h3 className="col-md-11">
        <span className="col-md-1 border-0">
          <i className="fa fa-circle mr-3" aria-hidden="true"></i>
        </span>
        <span className="col-md-10">{item}</span>
      </h3>
      <button
        className="offset-8 offset-md-0 col-4 col-md-1 btn btn-primary btn-sm smallbutton"
        onClick={e => {
          props.handleDelete(e, item);
        }}
      >
        Delete
      </button>
    </li>
  ));

  return (
    <div className="text-light col-12">
      <ul className="col-md-12 ml-0">{list}</ul>
    </div>
  );
}
export default List;
